* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html {
    font-family: 'Londrina Solid', cursive;
    font-family: 'Bungee Shade', cursive;
    font-size:62.5%;
}
body {
    background: #fff;
}
h1 {
    font:900 5.3rem "Roboto";
    color:#000;
    text-align: center;
}
h2 {
    font:900 5rem "Roboto";
    color:#404040;
}
h3 {
    font:500 3rem "Roboto";
    color:#404040;
}
p {
    font:normal 1.8rem "Roboto";
    color:#404040;
}
a {
    display: inline-block;
    text-decoration: none;
}
a.navLink:hover {
    text-decoration: underline;
  }
  .gridContainer {
    max-width: 1300px;
    margin: 0 auto;
  }
  .logo {
    width:20%;
    text-align: center;
    font:900 7rem "Londrina Solid";
    color:#000;
    margin:5px;
  }
  li {
    margin:15px;
    list-style: none;
  }
/*----------------------------- header ---------------------------*/
.header {
    padding:30px;
}
.flex1 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.navUp .navList {
    display:flex;
    justify-content: space-around;
}
.navUp {
   margin:5px;
}
.navLink {
    font:900 2.5rem "Bungee Shade";
    color:#35503f;
}
.flex2 .row,
.flex2 .row {
    display: flex;
    //flex-flow:wrap;
}
.news .article {
    width: 33.33%;
    height: 400px;
    box-shadow:20px 20px 20px 0px rgba(187,187,187,.85);
    margin: 15px;
    background: #000;
    position: relative;
    overflow:hidden;
}
.news .article {
    width: 50%;
}
.article .blockTextPost {
    width: 100%;
    height: 80px;
    position: absolute;
    text-align: right;
    right: 0;
    bottom: 0;
    background-color: #007F7FFF;
    padding:10px;
}
.row img {
    width: 100%;
    height: 100%;
    //transition:all 1s ease-out;

    transform: scale(1, 1);
    transition: transform 0.4s cubic-bezier(.165,.84,.44,1);
}
.news img:hover {
    transform: scale(1.03, 1.03);
}
.article .linkArticle {
    position: absolute;
    //width: 100%;
    //height:100%;
}
.wrapper .footer {
    padding:20px;
    background: #F77;
}





